import React from 'react'
import { Link } from "react-router-dom";


const Header = (props) =>{
    return(
        <div className="App">
              <div className='header'>
                <ul>
                  <li><Link to='combat'></Link></li>
                  <li><Link to='hunter'></Link></li>
                  <li><Link to='master'></Link></li>
                  <li><Link to='medic'></Link></li>
                  <li><Link to='survival'></Link></li>
                  <li><Link to='stalker'></Link></li>
                </ul>
              </div>
          </div>
    )
}

export default Header;